'use strict'

const PREFIX = 'hwstd' // homework for student

export function loadCredentials() {
    let credentials = {}
    let key = `${PREFIX}:name`
    let name = localStorage.getItem(key)
    if (null === name) return undefined

    key = `${PREFIX}:pin`
    let pin = localStorage.getItem(key)
    if (null === pin) {
        pin = ''
        localStorage.setItem(key, pin)
    }
    credentials.name = name
    credentials.pin = pin
    return credentials
}

export function saveCredentials(name, pin) {
    let key = `${PREFIX}:name`
    localStorage.setItem(key, name)
    key = `${PREFIX}:pin`
    localStorage.setItem(key, pin)
}

export function clearCredentials() {
    let key = `${PREFIX}:name`
    localStorage.removeItem(key)
    key = `${PREFIX}:pin`
    localStorage.removeItem(key)
}