'use strict'

import AWS from 'aws-sdk/global'
import * as sigv4 from '@/lib/sigv4'
//~ import store from '@/lib/store'
import {S3_REGION, S3_ENDPOINT_URL, S3_ACCESS_KEY_ID, S3_SECRET_ACCESS_KEY} from '@/config.js'

const INTERVAL = 3600000 // 60 minutes
//~ const GRACE = 60000 // 1 minute

export function setAwsConfig() {
    AWS.config.region = S3_REGION
    AWS.config.s3 = {
        endpoint: S3_ENDPOINT_URL,
        s3ForcePathStyle: true
    }
    AWS.config.accessKeyId = S3_ACCESS_KEY_ID
    AWS.config.secretAccessKey = S3_SECRET_ACCESS_KEY
    AWS.config.signatureVersion = 'v4'
    AWS.config.s3ForcePathStyle = true
}

