export function s3PreSignWCache(bucket, key, expires) {
  let nowTs = Date.now()
  let baseTime = nowTs - (nowTs % INTERVAL) // tail
  //~ let creds = store.state.stsCreds
  //~ let env = creds.env
  let _h = S3_ENDPOINT_URL.split('://')
  let protocol = _h[0]
  let host = bucket + '.' + _h[1]
  let path = '/' + key
  let url = sigv4.createPresignedURL('GET', host, path, 's3', 
      "UNSIGNED-PAYLOAD", {
    key: S3_ACCESS_KEY_ID,
    secret: S3_SECRET_ACCESS_KEY,
    protocol: protocol,
    timestamp: baseTime,
    region: S3_REGION,
    expires: expires,
    signSessionToken: true
  })
  return url
}
