<template>
  <v-container fluid>
    <v-row align="center" justify="center" v-show="'login'===activeSection">
      <v-card class="elevation-12">
        <v-card-text>
          <v-form>
            <v-text-field label="Nama Siswa" name="name" type="text"
              v-model="name" clearable
            />
            <v-text-field label="Nomor PIN (opsional)" name="pin" type="number"
            v-model="pin" clearable
            />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" :loading="loading" @click="btnSaveIdClick">
            Save
            <v-icon right>mdi-content-save</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
    <v-row v-show="'home'===activeSection">
      <v-col cols="12">
        <v-row align="center">
          <v-col cols="1">
            <v-icon>mdi-clipboard-text</v-icon>
          </v-col>
          <v-col cols="11">
            {{ assignmentName }}
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="1">
            <v-icon>mdi-account</v-icon>
          </v-col>
          <v-col cols="9">
            {{ name }}
          </v-col>
          <v-col cols="1">
            <v-btn icon :disabled="loading" @click="btnEditNameClick">
              <v-icon>mdi-pencil</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="1">
            <v-icon>mdi-stack-overflow</v-icon>
          </v-col>
          <v-col cols="9">
            {{ pages }} halaman
          </v-col>
          <v-col cols="1">
            <v-btn icon :disabled="loading" @click="btnAddPhotoClick">
              <v-icon>mdi-camera-plus</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-row align="center" v-show="pages>0">
          <v-col cols="12" align="center">
            <v-btn color="primary" class="mx-2" :disabled="loading"
              @click="btnResetClick"
            >
              Reset
              <v-icon right>mdi-restore</v-icon>
            </v-btn>
            <v-btn color="primary" class="mx-2" :loading="loading"
              @click="btnSendClick"
            >
              Kirim
              <v-icon right>mdi-send</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-row align="center" justify="center" v-show="loading">
          <span v-show="0===percent">Persiapan...</span>
          <span v-show="percent>0">Upload: {{ percent }}%</span>
        </v-row>
        <v-row align="center">
          <v-col cols="12" align="center">
            <v-btn color="primary" class="mt-2" v-show="!loading"
              @click="btnLogoutClick"
            >
              Keluar
              <v-icon right>mdi-exit-run</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-row v-show="'404'===activeSection" align="center" justify="center">
      <v-col cols="12" class="text-center">
        <v-icon x-large color="orange darken-2">mdi-hand</v-icon>
      </v-col>
      <v-col cols="12">
        Tidak ada pekerjaan rumah dengan link ini!
      </v-col>
    </v-row>
    <div v-show="false">
      <input
        type="file"
        accept="image/*"
        @change="onFileChange"
         capture="environment"
        ref="photo"
      >
    </div>
  </v-container>
</template>

<script>
"use strict"

import * as jsPDF from 'jspdf'
import S3 from 'aws-sdk/clients/s3'
import { S3_BUCKET } from '@/config.js'
import { setAwsConfig } from '@/lib/aws_lib.js'
import { clearCredentials, loadCredentials, saveCredentials } from '@/lib/storage.js'

export default {
  name: "Home",
  data: () => ({
    assignment: null,
    assignmentId: null,
    assignmentName: '',
    credFormMode: 'register',
    name: '',
    pin: '',
    old_name: '',
    old_pin: '',
    myfile: null,
    pdfDoc: null,
    pages: 0,
    percent: 0,
    managedUpload: null,
    activeSection: 'login',
    loading: false
  }),
  watch: {
    // eslint-disable-next-line
    $route(to, _from) {
      let self = this
      this.checkBucket()
        .then(() => {
          self.loadData()
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  created() {
    setAwsConfig()
    let self = this
    this.checkBucket()
      .then(() => {
        self.loadData()
      })
      .catch(error => {
        console.log(error)
      })
  },
  methods: {
    checkBucket() {
      return new Promise((resolve, reject) => {
        let params = {
          Bucket: S3_BUCKET
        }
        let s3 = new S3()
        // eslint-disable-next-line
        s3.headBucket(params, function(err, _data){
          if (err) {
            // TODO make bucket
            console.log(err)
            s3.createBucket(params).promise()
              // eslint-disable-next-line
              .then(data => {
                console.debug(S3_BUCKET, 'has been created')
                resolve()
              })
              .catch(error => {
                reject(error)
              })
              .then(() => {
                return
              })
          }
          resolve()
        })
      })
    },
    getAssignment() {
      let s3 = new S3()
      let params = {
        Bucket: S3_BUCKET,
        Key: `a/${this.assignmentId}.json`
      }
      let self = this
      this.loading = true
      s3.getObject(params).promise()
        .then(response => {
          self.assignment = JSON.parse(response.Body.toString('utf-8'))
          self.assignmentName = self.assignment.name
        })
        // eslint-disable-next-line
        .catch(error => {
          self.activeSection = '404'
        })
        .then(() => {
          self.loading = false
        })
    },
    loadData() {
      //~ setAwsConfig()
      let creds = loadCredentials()
      if (undefined === creds) {
        this.name = ''
        this.pin = ''
        this.credFormMode = 'register'
        this.activeSection = 'login'
        return
      } else {
        this.name = creds.name
        this.pin = creds.pin
        this.activeSection = 'home'
      }
      this.assignmentId = this.$route.query.a
      if (undefined === this.assignmentId) {
        this.activeSection = '404'
        return
      }
      this.getAssignment()
    },
    btnSaveIdClick() {
      if (this.name === '') return
      this.assignmentId = this.$route.query.a
      if (this.credFormMode === 'register') {
        saveCredentials(this.name, this.pin)
        if (undefined === this.assignmentId) {
          this.activeSection = '404'
        } else {
          this.activeSection = 'home'
          this.getAssignment()
        }
      } else {
        // this.credFormMode = 'edit'
        let modified = false
        if (this.name !== this.old_name) {
          modified = true
        }
        if(this.pin !== this.old_pin) {
          modified = true
        }
        if (modified) {
          saveCredentials(this.name, this.pin)
        }
        this.activeSection = 'home'
        this.getAssignment()
      }
    },
    btnEditNameClick() {
      this.credFormMode = 'edit'
      this.old_name = this.name
      this.old_pin = this.pin
      this.activeSection = 'login'
    },
    btnAddPhotoClick() {
      this.$refs.photo.click()
    },
    onFileChange: function(event) {
      if (event.target.files.length > 0) {
        let self = this
        let reader = new FileReader()
        reader.onload = function() {
          if (null === self.pdfDoc) {
            self.pdfDoc = new jsPDF('p', 'mm', 'legal')
          } else {
            self.pdfDoc.addPage('legal')
          }
          self.pdfDoc.addImage(
            reader.result, 'JPEG', 0, 0, 216, 356
          )
          self.pages = self.pages + 1
        }
        reader.readAsDataURL(event.target.files[0])
      }
    },
    btnSendClick: async function() {
      if (null === this.pdfDoc) return
      if (0 === this.pages) return
      let fname = ''
      if ('' === this.pin) {
        fname = `${this.name}.pdf`
      } else {
        fname = `${this.name} ${this.pin}.pdf`
      }
      let s3 = new S3()
      let s3_key = `a/${this.assignmentId}/${fname}`
      let blob = this.pdfDoc.output('blob')
      let params = {
        Bucket: S3_BUCKET,
        Key: s3_key,
        ContentType: 'application/pdf',
        Body: blob
      }
      this.managedUpload = s3.upload(params)
      let self = this
      this.managedUpload.on('httpUploadProgress', progress => {
        self.percent = Math.round(100*progress.loaded/progress.total)
      })
      this.percent = 0
      this.loading = true
      try {
        await this.managedUpload.promise()
        this.loading = false
        this.pdfDoc = null
        this.pages = 0
        let presignedUrl = s3.getSignedUrl(
          'getObject',
          {Bucket: S3_BUCKET, Key: s3_key, Expires: 86400}
        )
        this.$router.replace({
          name: 'success',
          query: {
            u: btoa(presignedUrl)
          }
        })
      } catch(e) {
        console.error(e)
        this.loading = false
      }
    },
    btnResetClick() {
      this.pdfDoc = null
      this.pages = 0
    },
    btnLogoutClick() {
      this.activeSection = 'login'
      clearCredentials()
      this.name = ''
      this.pin = ''
      this.pdfDoc = null
      this.pages = 0
    },
  }
}
</script>
